@import 'reset_css';
@import 'variables';

.container {
    width: 280px;
    margin: 0 auto;
}

.topBar {
    width: 100%;
    background-color:  #f6f6f6;
    height: 27px;
}

.topBarMenuLeft {
    @extend %topBarMenu;
    justify-content: space-between;
    @include topBarFont;
}

.topBarMenuRight {
    @extend %topBarMenu;
    justify-content: space-evenly;
    margin-top: 11px;
}

.topBarMenuRight__dropDown {
    box-sizing: border-box;
    padding: 9px 11px 15px;
    @include topBarFont;
}

.topBarMenuRight__item {

    &:focus {
        outline: none;
    }

    &::-webkit-details-marker { 
        display: none;
    }

    &::after {
        content: '\25BC';
        padding-left: 0.5em;
        font-size: 12px;
    }
}

details[open] .topBarMenuRight__item::after {
    content: '\25B2';
}

.topBarMenuRight__itemLi {
    @include topBarFont;
}

.topBarMenuRight__dropDown_border {
    border: 1px solid #bbbbbb;
    background-color:  #ffffff;
}

