@font-face {
    font-family: MerriweatherSans-Regular;
    src: url(../fonts/merriweatherSans/MerriweatherSans-Regular.ttf);
}
@font-face {
    font-family: MerriweatherSans-LightItalic;
    src: url(../fonts/merriweatherSans/MerriweatherSans-LightItalic.ttf);
}
@font-face {
    font-family: MerriweatherSans-Italic;
    src: url(../fonts/merriweatherSans/MerriweatherSans-Italic.ttf);
}
@font-face {
    font-family: MerriweatherSans-ExtraBold;
    src: url(../fonts/merriweatherSans/MerriweatherSans-ExtraBold.ttf);
}
@font-face {
    font-family: MerriweatherSans-BoldItalic;
    src: url(../fonts/merriweatherSans/MerriweatherSans-BoldItalic.ttf);
}
@font-face {
    font-family: MerriweatherSans-Bold;
    src: url(../fonts/merriweatherSans/MerriweatherSans-Bold.ttf);
}

%topBarMenu {
    display: flex;
    align-items: center;
    height: 27px;
}

@mixin topBarFont {
    font-family: MerriweatherSans, sans-serif;
    color:  #7e7e7e;
    font-size: 11px;
    font-weight: 400;
    line-height: 19px;
    text-transform: uppercase;
}